// пропускает строку с двумя словами разделенными пробелами и состоящие только из латинских букв
export function validateCardHolder(name) {
  return /^[A-Za-z]+ [A-Za-z]+$/.test(name.trim());
}
// не пропускает произвольную строку, содержащую любые нецифровые символы.
export function validateCardNumber(number) {
  const digits = number.replace(/\s+/g, "");
  return /^\d{16}$/.test(digits);
}

export function validateCVV(cvv) {
  return /^\d{3}$/.test(cvv);
}
